.TodoItem {
    background-color: #003566;
    border-radius: 12px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 96px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--completed {
    text-decoration: line-through;
    color: rgb(60, 177, 60);
  }
  
  .Icon {
    cursor: pointer;    
    /* SVG Exclusive  */
    stroke:  #ffc300;
    fill: #ffc300;    
  }
  
  .Icon-check {
    transform: rotate(-15deg);
    position: absolute;
    left: 18px;
    stroke-width: 25;
    height: 20px;
    width: 20px; 
  }

  .Icon-check--active {
    stroke: rgb(60, 177, 60);
    animation: ready 500ms ease-in-out 10ms 1 normal none;
    /* animation: name duration timing-function delay iteration-count direction fill-mode; */
  }

  @keyframes ready {
    0% {
      transform: scale(0.7);
    }
    50% {
      transform: scale(2);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .Container__Icon-delete {
    height: 40px;
    width: 26px;
    right: 10px;
    stroke-width: 13;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: absolute;    
  }
  
  .Container__Icon-delete--cover {    
    height: 15px;
    transform: rotate(90deg);
    position: absolute;
    bottom: 10px;
    right: 10px;
  }
  
  .Container__Icon-delete--basket {
    height: 15px;
  }
  .Container__Icon-delete:hover {
    stroke: red;
    fill: red;    
  }

  .Container__Icon-delete:hover .Container__Icon-delete--cover{    
    left: 0;
    transform: rotate(0deg);   
  }

  .Animation__Delete {
    stroke: red;
    fill: red; 
    animation: trash; 
    animation-duration: 700ms;
    animation-timing-function: ease-in;
    animation-fill-mode: forwards;
  }

  @keyframes trash {      
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.4);
    }    
    100% {
      transform: scale(0.5);
    }
  }